import axios from 'axios'
import React, { useState, useEffect } from 'react'
import personsService from './services/persons'

//Components
const Filter = ({ value, onChange }) => {
    return (
        <form>
            Find person: <input value={value} onChange={onChange} />
        </form>
    )
}

const DisplayPersons = ({ filterName, persons }) => {
    const filtered = persons.filter(el => el.name.toLowerCase().includes(filterName.toLowerCase()))

    if (filterName.length === 0) {
        return (
            <div></div>
        )
    } else {
        return (
            <div>
                {filtered.map(el => {
                    return (
                        <p key={el.id}>{el.name}: {el.number}</p>
                    )
                })}
            </div>
        )
    }
}

const Notification = ({ person }) => {
    const notificationStyle = {
        color: 'green',
        border: 'solid',
        borderRadius: 8,
        backgroundColor: 'lightgrey',
        padding: 12
    }

    if (person === '') {
        return (
            <div></div>
        )
    }
    return (
        <p style={notificationStyle}>{person}</p>
    )
}

const AddPerson = ({ value, onChange, onSubmit, numberChange, typeNumber }) => {
    return (
        <form onSubmit={onSubmit}>
            <div>
                Name: <input value={value} onChange={onChange} />
            </div>
            <div>
                Number: <input type="number" value={typeNumber} onChange={numberChange} />
            </div>
            <div>
                <button type="submit">Add</button>
            </div>
        </form>
    )
}

const DisplayNumbers = ({ persons, deleteButton }) => {
    return (
        <div>
            <h2>Numbers</h2>
            {persons.map(el => {
                return (
                    <p key={el.id}>{el.name}: {el.number} <button name={el.name} id={el.id} onClick={deleteButton}>Delete</button></p>
                )
            })}
        </div>
    )
}

const App = () => {
    const [persons, setPersons] = useState([])
    const [filterName, setFilterName] = useState('')
    const [notification, setNotification] = useState('')

    useEffect(() => {
        personsService
            .getAll()
            .then(initialperson => {
                console.log('server data loaded')
                setPersons(initialperson)
            })
    }, [])

    const addName = (event) => {
        const newName = event.target[0].value
        const newNumber = event.target[1].value

        event.preventDefault()

        //Creates a new nameObject that we can add to *persons*
        const personsObject = {
            name: newName,
            number: newNumber,
            id: persons.length + 1
        }

        //Checks if the name exists in database
        if (persons.map(el => el.name.toLowerCase()).includes(newName.toLowerCase())) {
            if (window.confirm(`'${newName}' aldready added to phonebook, replace the old number with a new one?`)) {
                //Index of entry
                const entry = persons.filter(el => el.name.toLowerCase().includes(newName.toLowerCase()))
                const changedNumber = { ...entry[0], number: `${newNumber}` }

                axios
                    .put(`http://localhost:3001/persons/${entry[0].id}`, changedNumber)
                    .then(response => {
                        const updated = persons.map(el => el.id !== response.data.id ? el : response.data)
                        setPersons(updated.map(el => el))
                    })
                    .catch(error => {
                        setNotification(newName + ' has already been deleted.')
                        setPersons(persons.filter(el => el.id !== entry[0].id))
                        setTimeout(() => {
                            setNotification('')                            
                        }, 3000)
                    })

                event.target[0].value = ''
                event.target[1].value = ''
            }

        } else if (newName === '' || newNumber === '') {
            alert('Name or number is missing.')
        } else {
            personsService
                .create(personsObject)
                .then(returnedPersons => {
                    console.log('posted')
                    setPersons(persons.concat(returnedPersons))
                    event.target[0].value = ''
                    event.target[1].value = ''

                    setNotification(newName + ' has been added.')
                    setTimeout(() => {
                        setNotification('')
                    }, 3000)
                })
        }
    }

    const handleFilterChange = (event) => {
        setFilterName(event.target.value)
    }

    const toggleDelete = (event) => {
        if (window.confirm(`Delete '${event.target.name}'?`)) {
            personsService
                .deleteNumber(event.target.id)
                .then(() => {
                    console.log("deleted")
                    //Button id
                    const targetId = parseInt(event.target.id)
                    //The index of the button-id in the array
                    const targetInd = persons.map(el => el.id).indexOf(targetId)
                    //Remove it from array
                    persons.splice(targetInd, 1)
                    setPersons(persons.map(el => el))
                })
        }
    }

    return (
        <div>
            <h2>Phonebook</h2>
            <Notification person={notification} />
            <Filter value={filterName} onChange={handleFilterChange} />
            <DisplayPersons filterName={filterName} persons={persons} />
            <h2>Add a new number</h2>
            <AddPerson onSubmit={addName} />
            <DisplayNumbers persons={persons} deleteButton={toggleDelete} />
        </div>
    )
}


export default App